.container-team {
  height: 100vh;
  background: url('../../../../../assets/img/bkg-branco-1920.png');
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 2rem;
}

.grid-team {
  display: grid;
  grid-template-columns: 1fr, 1fr, 1fr;
  grid-template-rows: 1fr, 1fr;
  grid-template-areas: 
  "card-1 card-2 card-3"
  "card-4 card-5 card-6";
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  gap: 36px;
}

.grid-team > .card-1,
.grid-team > .card-2,
.grid-team > .card-3 {
  background: url('../../../../../assets/img/team-card1.png');
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 26rem;
  height: 38rem;
  padding: 16px;
  font-size: 1.5rem;
  text-align: justify;
}

.grid-team > .card-4,
.grid-team > .card-5,
.grid-team > .card-6 {
  width: 26rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--font-secondary);
  font-size: 1.5rem;
}

.grid-team > .card-1 > img,
.grid-team > .card-2 > img,
.grid-team > .card-3 > img {
  width: 10rem;
  height: 10rem;
  margin: 12px 0 12px 0;
  border: 0.2rem solid #fff;
  border-radius: 50%;
}

.grid-team > .card-4 > img,
.grid-team > .card-5 > img,
.grid-team > .card-6 > img {
  width: 20rem;
  height: 17rem;
}

.grid-team > .card-1 {
  grid-area: "card-1";
}
.grid-team > .card-2 {
  grid-area: "card-2";
}
.grid-team > .card-3 {
  grid-area: "card-3";
}
.grid-team > .card-4 {
  grid-area: "card-4";
}
.grid-team > .card-5 {
  grid-area: "card-5";
}
.grid-team > .card-6 {
  grid-area: "card-6";
}

.grid-team > .card {
  display: flex;
  flex-direction: column;
}

.container-team > h1,
.container-team > p {
  color: var(--font-secondary);
}

.container-team > h1 {
  font-size: 8rem;
  text-align: center;
  margin: 16px 0 16px 0;
}

.container-team > p {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 16px;
}