.container-what-is-final {
  height: 100vh;
  background: url('../../../../../assets/img/bkg-branco-1920.png');
  background-repeat: no-repeat;
  background-size: cover;
  color: var(--font-secondary);
  font-size: 2rem;
}


.container-what-is-final > section > h1 {
  margin: 16px;
  font-size: 8rem;
  font-weight: bold;
  text-align: center;
}

.container-what-is-final > section > p {
  margin: 8px;
  font-size: 2.8rem;
  text-align: justify;
}

.container-what-is-final > section {
  margin: auto;
  width: 80%;
}
