.container-roadmap {
  height: 100vh;
  background: url('../../../../../assets/img/bkg-branco-1920.png');
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 2rem;
  color: var(--font-secondary);
}


.container-roadmap > h1 {
  font-size: 8rem;
  text-align: center;
}

.container-roadmap > p {
  font-size: 2rem;
  text-align: center;
}

.container-roadmap > .row > .card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0a74cb7e;
  padding: 16px;
  width: 40rem;
  height: 16rem;
  border-radius: 40px 10px 40px 10px;
  color: var(--font-primary);
  font-size: 1.5rem;
  text-align: center;
}

.container-roadmap > .row > .circle {
  width: 9rem;
  height: 9rem;
  border-radius: 50%;
  border: 2px solid #282828;
  background-color: transparent;

  display: flex;
  justify-content: center;
  align-items: center;
}

.container-roadmap > .row-1 {
  display: flex;
  justify-content: center;
  margin: 4rem;
}

.container-roadmap > .row-1 > .spacer {
  display: block;
  width: 30rem;
  margin-bottom: 8rem;
}

.container-roadmap > .row-1 > .card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0a74cb7e;
  width: 40rem;
  height: 16rem;
  border-radius: 40px 10px 40px 10px;
  margin: 0 32px 0 32px;
  color: var(--font-primary);
  font-size: 1.5rem;
  text-align: center;
}
  
.container-roadmap > .row {
  display: flex;
  justify-content: space-around;
  margin-top: 6rem;
}